<template>
    <div class="Market">

        <a-row class="mt-60">

            <a-col :span="24">

                <h3>Password Recovery E-mail</h3>
                <a-col :span="8" :offset="8">
                    <a-alert v-if="alert" message="The system has sent an email to your mailbox, click the link to reset the password!" type="success" show-icon />

                    <a-form id="components-form-demo-normal-login" :form="form" class="login-form t-left" @submit="handleSubmit">
                        <a-form-item>
                            <a-input :disabled="alert" v-decorator="[
                            'Email',
                            { rules: [
                            { type: 'email', message: 'The input is not valid E-mail!' },
                            { required: true, message: 'Please input your Email!' }
                            ] } ]" placeholder="Email">
                                <a-icon slot="prefix" type="mail" style="color: rgba(0,0,0,.25)" />
                            </a-input>
                        </a-form-item>

                        <a-form-item>
                            <a-row>
                                <a-button :loading="loading" :disabled="alert" type="primary" html-type="submit" class="login-form-button" block>Submit</a-button>
                            </a-row>
                        </a-form-item>

                        <a-form-item>
                            <a-row>
                                <a-col :span="12" class="t-left">
                                    <router-link to="/login">Login</router-link>
                                </a-col>
                                <a-col :span="12" class="t-right">
                                    <router-link to="/register">Register</router-link>
                                </a-col>
                            </a-row>
                        </a-form-item>

                    </a-form>
                </a-col>

            </a-col>

        </a-row>

        <div style="margin-top: 500px;"></div>
    </div>
</template>


<script>
export default {
    name: "Market",
    components: {},
    beforeCreate() {
        this.form = this.$form.createForm(this, { name: "forgotpassword" });
    },
    data() {
        return {
            loading: false,
            alert: false,
        };
    },
    methods: {
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFieldsAndScroll((err, values) => {
                if (err) {
                    console.error(err);
                    return;
                }

                this.loading = true;
                $.ajax({
                    url: `${this.g.apiUrl}/api/forgot-password`,
                    data: values,
                    type: "post",
                    error: (XMLHttpRequest, textStatus, errorThrown) => {
                        this.loading = false;
                        alert(XMLHttpRequest.responseJSON.message);
                    },
                    success: (res) => {
                        this.loading = false;
                        if (res.flag != "ok") {
                            alert(res.flag);
                            return;
                        }
                        this.alert = true;
                        // this.$message.success("The system has sent an email to your mailbox, click the link to reset the password!");
                    },
                });
            });
        },
        init() {
            //
        },
    },
    mounted() {
        this.init();
    },
};
</script>



